@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
@font-face {
  font-family: "Latinka SemiBold";
  font-weight: "bold";
  src: url(/static/media/Latinka-SemiBold.50b703d1.ttf); }

@font-face {
  font-family: "Latinka";
  font-weight: "bold";
  src: url(/static/media/Latinka-Regular.4ca1a812.ttf); }

body {
  margin: 0;
  font-family: "Latinka", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

h1 {
  font-size: 48px;
  margin-top: 0px;
  margin-bottom: 0px; }

h2 {
  font-size: 40px;
  margin-top: 0px;
  margin-bottom: 0px; }

h3 {
  font-size: 36px;
  margin-top: 0px;
  margin-bottom: 0px; }

.home-component {
  font-family: 'Latinka';
  height: 100vh;
  color: #FFF; }
  .home-component .section-work {
    background: #DF6E21;
    display: flex;
    flex-direction: column;
    justify-content: center; }
    .home-component .section-work .work-svg {
      max-height: 300px;
      width: auto;
      margin-bottom: 40px; }
    .home-component .section-work p.title {
      font-size: 24px;
      margin-bottom: 20px;
      position: relative;
      letter-spacing: 2px; }
      .home-component .section-work p.title::before {
        content: '';
        display: block;
        position: absolute;
        bottom: 0;
        left: 0;
        width: 30px;
        height: 3px;
        background: #fff; }
  .home-component .section-vision {
    height: 50vh;
    background: #2096BA;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative; }
    .home-component .section-vision .vision-svg {
      max-width: 350px;
      height: auto; }
    .home-component .section-vision p.title {
      font-size: 20px;
      margin-bottom: 20px;
      position: relative;
      letter-spacing: 2px; }
      .home-component .section-vision p.title::before {
        content: '';
        display: block;
        position: absolute;
        bottom: 0;
        left: 0;
        width: 30px;
        height: 3px;
        background: #fff; }
    .home-component .section-vision h3 {
      font-size: 28px; }
  .home-component .content-container {
    position: absolute;
    bottom: 20px; }
  .home-component .section-team {
    background: #252b61; }

.ComingSoon {
  text-align: center;
  background: #DF6E21; }

.App-logo {
  height: 8rem; }

@media only screen and (max-width: 600px) {
  .App-logo {
    width: 90%;
    height: auto; } }

.script-box {
  will-change: width, height, left, top;
  position: relative; }

.test-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%;
  position: fixed;
  top: 0px; }

p {
  color: #FFF;
  font-size: 24px; }

.animate-container {
  position: fixed;
  top: 0px;
  height: 100%;
  width: 100%;
  background: #DF6E21; }

.header-component {
  position: absolute;
  top: 0px;
  width: 100%;
  padding: 40px; }

